*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --main-background-color: #fff;
  --secondary-background-color: #fff5f6;
  --hero-and-ice-cream-bg-color: #ffb8ca;
  --color-hero-elips-and-label-and-time: #ffa5ba;
  --bg-color-ice-coffe-and-cafe: #f0d1a5;
  --bg-color-milkshakes-and-food-truck: #c2e297;
  --bg-color-revues: #fafafa;
  --bg-color-revues-circle: #d9d9d9;
  --bg-color-revues-point: #c7133e;
  --bg-color-revues-house: #d31342;
  --accent-color: #d41443;
  --secondary-text-color: #907e82;
  --main-text-color: #000;
  --text-color-white: #fff;
  --hover-bg-color-button: #ecebeb;
  --footer-bg-color-button: #d41342;
  --footer-bg-color-button-hover: rgba(212, 19, 66, .5);
  --bg-color-link-socials: #fea5bb;
  --color-textarea: rgba(117, 117, 117, .5);
  --mobile-menu-bg-color: #e17992;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.form__label-checkbox, .modal__close-button, .mobile-menu__close-menu, .page-header__open-menu {
  justify-content: center;
  align-items: center;
  display: flex;
}

*, :after, :before {
  box-sizing: inherit;
}

ul {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

address {
  font-style: normal;
}

body {
  letter-spacing: .04em;
  background-color: var(--main-background-color);
  color: var(--main-text-color);
  font-family: DM Sans, sans-serif;
  font-size: 16px;
  position: relative;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.section {
  padding-top: 30px;
  padding-bottom: 30px;
}

@media screen and (min-width: 1200px) {
  .section {
    padding-top: 60px;
    padding-bottom: 60px;
  }
}

.link {
  letter-spacing: 0;
  color: var(--text-color-white);
  cursor: pointer;
  font-weight: 700;
  line-height: 1.3125;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

.link:hover, .link:focus {
  color: var(--accent-color);
}

.button {
  min-height: 44px;
  letter-spacing: 0;
  background-color: var(--main-background-color);
  color: var(--accent-color);
  text-align: center;
  cursor: pointer;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 22px;
  padding-left: 37px;
  padding-right: 37px;
  font-family: inherit;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.3125;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

.button:hover, .button:focus {
  background-color: var(--hover-bg-color-button);
}

.button__icon--right {
  margin-left: 12px;
}

.title {
  letter-spacing: .04em;
  text-align: center;
  text-transform: uppercase;
  color: var(--accent-color);
  font-family: Titan One, sans-serif;
  font-size: 34px;
  font-weight: 400;
  line-height: 1.147;
}

@media screen and (min-width: 768px) {
