  }
}

.title--smaller {
  letter-spacing: 0;
  color: var(--color-hero-elips-and-label-and-time);
  font-size: 18px;
  line-height: 1.17;
  display: block;
}

@media screen and (min-width: 768px) {
  .title--smaller {
    font-size: 26px;
    line-height: 1.15;
  }
}

@media screen and (min-width: 1200px) {
  .title--smaller {
    letter-spacing: .04em;
    font-size: 30px;
    line-height: 1.13;
  }
}

.icon {
  fill: currentColor;
}

.backdrop {
  width: 100%;
  height: 100%;
  opacity: 1;
  visibility: visible;
  background-color: rgba(0, 0, 0, .2);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  transform: scale(1);
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal {
  position: absolute;
  top: 50%;
  left: 50%;
}

.form-group {
